import 'bootstrap/dist/css/bootstrap.min.css'
import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'
import {Button, Form} from 'react-bootstrap'

const ONE = new Web3.utils.BN('1000000000000000000')
const ZERO_ADDRESS = '0x0000000000000000000000000000000000000000'
const MAX = '0XFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF'

function nn(value, unit='ether') {
    return value ? Web3.utils.fromWei(value.toString(), unit) : undefined
}

function bb(value, unit='ether') {
    return value ? Web3.utils.toWei(value.toString(), unit) : undefined
}

function App() {

    const web3 = new Web3(window.ethereum)

    const washTrade = new web3.eth.Contract(abis.WashTrade, '0xD5e689C846ec947f8CC6e8Af3185DDCEcFdaf908')

    const [data, setData] = useState({
        account: ZERO_ADDRESS,
        pool: ZERO_ADDRESS,
        symbol: 'BTCUSD-60000-C',
        idealVolume: 0,
        margin: 0,
        requiredMargin: 0,
        realVolume: 0,
        percent: 0,
        oracles: ''
    })

    useEffect(() => {(async () => {
        const account = await Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
        setData({...data, account})
    })()}, [])

    const getOracleSignatures = async () => {
        const signatures = []
        const urls = [
            'https://oraclemainnet.deri.finance/get_signed_volatility_v3?symbol=VOL-BTCUSD',
            'https://oraclemainnet.deri.finance/get_signed_volatility_v3?symbol=VOL-ETHUSD'
        ]
        await Promise.all(urls.map(async (url) => {
            const res = await fetch(url)
            const sig = await res.json()
            signatures.push([sig.data.symbolId, sig.data.timestamp, sig.data.volatility, sig.data.v, sig.data.r, sig.data.s])
        }))
        return signatures
    }

    const onPopulate = async () => {
        const account = Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
        const pool = data.pool === ZERO_ADDRESS ? '0x243681B8Cd79E3823fF574e07B2378B8Ab292c1E' : data.pool
        const info = await washTrade.methods.queryFreeTrade(pool, data.symbol).call()

        let realVolume = nn(info.margin) / nn(info.requiredMargin) * nn(info.volume) * 0.99
        if (Math.abs(nn(info.volume)) <= Math.abs(realVolume)) {
            realVolume = nn(info.volume)
        } else {
            realVolume = (Math.round(realVolume * 100) / 100).toString()
        }

        let oracles = await getOracleSignatures()

        setData({
            ...data,
            account: account,
            pool: pool,
            idealVolume: nn(info.volume),
            margin: nn(info.margin),
            requiredMargin: nn(info.requiredMargin),
            realVolume: realVolume,
            oracles: JSON.stringify(oracles)
        })
    }

    const onTrade = async () => {
        await washTrade.methods.trade(
            data.pool,
            data.symbol,
            bb(data.realVolume),
            JSON.parse(data.oracles)
        ).send({from: data.account})
    }

    return (
        <div className="App">
            <h1>Test Trade (20220124)</h1>
            <br/>
            <Form.Label>Address</Form.Label>
            <Form.Control value={data.account} onChange={(e) => {setData({...data, account: e.target.value})}} />
            <br />
            <Form.Label>Pool Address</Form.Label>
            <Form.Control value={data.pool} onChange={(e) => {setData({...data, pool: e.target.value})}} />
            <br />
            <Form.Label>Symbol</Form.Label>
            <Form.Control value={data.symbol} onChange={(e) => {setData({...data, symbol: e.target.value})}} />
            <br />
            <Form.Label>Ideal Volume</Form.Label>
            <Form.Control value={data.idealVolume} onChange={(e) => {setData({...data, idealVolume: e.target.value})}} />
            <br />
            <Form.Label>Your Margin</Form.Label>
            <Form.Control value={data.margin} onChange={(e) => {setData({...data, margin: e.target.value})}} />
            <br />
            <Form.Label>Reqiured Margin</Form.Label>
            <Form.Control value={data.requiredMargin} onChange={(e) => {setData({...data, requiredMargin: e.target.value})}} />
            <br />
            <Form.Label>Real Volume</Form.Label>
            <Form.Control value={data.realVolume} onChange={(e) => {setData({...data, realVolume: e.target.value})}} />
            <br />
            <Form.Label>Oracles</Form.Label>
            <Form.Control value={data.oracles} onChange={(e) => {setData({...data, oracles: e.target.value})}} />
            <br /><br />
            <Button onClick={() => onPopulate()}>Populate</Button>
            <Button onClick={() => onTrade()}>Trade</Button>
        </div>
    )

}

export default App
